import React from 'react';
import firestore from '../Vendor/Firestore';

const Create = () => {
  const news = {};

  function handleChange(e) {
    news[e.target.id] = e.target.value;
  }

  function handleSubmit(e) {
    e.preventDefault();
    news.timestamp = new Date();

    news.content &&
      news.title &&
      firestore
        .collection('news')
        .add(news)
        .then(() => {
          document.querySelector('#content').value = '';
          document.querySelector('#title').value = '';
        });
  }

  return (
    <form className="form white" onSubmit={handleSubmit}>
      <h2 className="grey-text text-darken-3">Create News</h2>
      <div className="input-field">
        <label htmlFor="title">Title</label>
        <input id="title" type="text" onChange={handleChange} />
      </div>
      <div className="input-field">
        <label htmlFor="content">Content</label>
        <input id="content" type="text" onChange={handleChange} />
      </div>
      <div className="input-field">
        <button className="btn pink lighten-1 z-depth-0">Create</button>
      </div>
    </form>
  );
};

export default Create;

